body {
  background-color: #444444;
  font-family: monospace;
}

.containerMessage {
  top: 5em;
  bottom: 6em;
  width: fit-content;
  height: fit-content;
  margin-left: auto;
  margin-right: auto;
  margin-top: 6%;
}

.bar {
  height: 2em;
  background-color: #dad9d9;
  border-radius: 5px 5px 0 0;
}
.icon {
  border-radius: 50%;
  width: 15px;
  height: 15px;
  margin: 5px 5px 2px 0;
  display: inline-block;
  vertical-align: middle;
}

.icon-red {
  margin-left: 20px;
  background-color: #e94b35;
}
.icon-yellow {
  background-color: #f2cb41;
}

.icon-green {
  background-color: #1aaf5c;
}

.screen {
  color: #ddd;
  font-size: 30px;
  word-spacing: 0px;
  cursor: default;
  border-radius: 0 0 5px 5px;
  overflow-y: auto;
  background-color: #545650;
  padding: 1em;
}

.screen a {
  list-style-type: none;
  list-style: none;
  color: #76eb15;
  cursor: pointer;
}

.screen i {
  color: red;
}

@media only screen and (max-height: 600px) {
  .screen {
    margin-top: 0;
    font-size: 20px;
    padding-top: 1%;
    padding-bottom: 1%;
    padding-right: 4%;
    padding-left: 4%;
  }
  .containerMessage {
    margin-top: 4%;
  }
}

@media only screen and (min-height: 600px) and (max-height: 750px) {
  .screen {
    margin-top: 0;
    font-size: 22px;
    padding-top: 1%;
    padding-bottom: 1%;
    padding-right: 4%;
    padding-left: 4%;
  }
  .containerMessage {
    margin-top: 5%;
  }
}

@media only screen and (min-height: 750px) and (max-height: 850px) {
  .screen {
    margin-top: 0;
    font-size: 26px;
    padding-top: 2%;
    padding-bottom: 2%;
    padding-right: 5%;
    padding-left: 5%;
  }
  .containerMessage {
    margin-top: 6%;
  }
}
